import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AlertController, ModalController, Platform } from '@ionic/angular';
import { ModalTrabajadoresComponent } from 'src/app/components/modal-trabajadores/modal-trabajadores.component';
import { DescripcionActividad } from 'src/app/interfaces/descripcion-actividad';
import { EDGuardarPermisoTrabajoAPP } from 'src/app/interfaces/edguardar-permiso-trabajo-app';
import { DataQuestionsService } from 'src/app/services/SST/data-questions.service';
import { SaveDataService } from 'src/app/services/SST/save-data.service';
import { TablaTrabajadoresService } from 'src/app/services/SST/tabla-trabajadores.service';
import { UtilsService } from 'src/app/services/utils.service';

@Component({
  selector: 'app-descripcion-actividad',
  templateUrl: './descripcion-actividad.page.html',
  styleUrls: ['./descripcion-actividad.page.scss'],
})
export class DescripcionActividadPage implements OnInit {
  questions: DescripcionActividad[];
  data: EDGuardarPermisoTrabajoAPP;
  submitted = false;

  actualizarContador(event, question: DescripcionActividad) {
    const contador = event.target.value.length;
    question.contador = contador;
  }

  constructor(
    private questionService: DataQuestionsService,
    private utils: UtilsService,
    private alertController: AlertController,
    private router: Router,
    public modalController: ModalController,
    private dataService: SaveDataService,
    private tablaTrabajadoresService: TablaTrabajadoresService
  ) {}

  ngOnInit() {
    this.getQuestions();
  }

  verTrabajadores() {
    this.modalController
      .create({
        component: ModalTrabajadoresComponent,
      })
      .then(modal => {
        modal.present();
      });
  }

  private async getQuestions() {
    /* data por URL */
    const dataUrl = history.state.data;
    await this.utils.presentLoading();
    this.questionService.getDescripcionActividad().subscribe(
      data => {
        this.questions = data;
        this.questions.forEach(question => (question.isValid = true));
        if (dataUrl) {
          this.questions[0].strRespuesta = dataUrl.strTipoTrabajo;
          this.questions[0].contador = dataUrl.strTipoTrabajo.length;
          this.questions[1].strRespuesta = dataUrl.strAntesActividad;
          this.questions[1].contador = dataUrl.strAntesActividad.length;
          this.questions[2].strRespuesta = dataUrl.strDuranteActividad;
          this.questions[2].contador = dataUrl.strDuranteActividad.length;
          this.questions[3].strRespuesta = dataUrl.strDespuesActividad;
          this.questions[3].contador = dataUrl.strDespuesActividad.length;
          this.dataService.setData(dataUrl);
          this.tablaTrabajadoresService.setListaTrabajadores(dataUrl['listaTrabajadoresMigrados']);
        }
        this.utils.dismissLoading();
      },
      error => {
        console.log(error);
      }
    );
  }

  async checkQuestions(questions: DescripcionActividad[]): Promise<boolean> {
    this.submitted = true;
    for (const question of questions) {
      this.questions.forEach(question => this.checkValidity(question));
      if (!question.strRespuesta) {
        await this.mostrarError('Debe responder todas las preguntas');
        return false;
      }
    }
    return true;
  }

  continuar() {
    this.checkQuestions(this.questions).then(async result => {
      if (result) {
        await this.guardarDatos();
        this.router.navigate([
          '/home/permisos/registro-permiso/crear-permiso/inicio-permiso/seccion-general/seccion-multiusos/seccion-plataforma/descripcion-actividad/proteccion-colectiva',
        ]);
      }
    });
  }

  private async mostrarError(message: string) {
    const alert = await this.alertController.create({
      header: 'Alerta',
      message: message,
      buttons: ['OK'],
    });
    alert.present();
  }

  async guardarDatos() {
    this.data = this.dataService.getData();
    this.data['strTipoTrabajo'] = this.questions[0].strRespuesta;
    this.data['strAntesActividad'] = this.questions[1].strRespuesta;
    this.data['strDuranteActividad'] = this.questions[2].strRespuesta;
    this.data['strDespuesActividad'] = this.questions[3].strRespuesta;
    this.data['lastRoute'] =
      '/home/permisos/registro-permiso/crear-permiso/inicio-permiso/seccion-general/seccion-multiusos/seccion-plataforma/descripcion-actividad';
    this.dataService.setData(this.data);
    this.dataService.saveData();
  }

  back() {
    this.router.navigate(
      ['/home/permisos/registro-permiso/crear-permiso/inicio-permiso/seccion-general/seccion-multiusos/seccion-plataforma'],
      { state: { data: this.data } }
    );
  }

  checkValidity(question) {
    // Aquí va tu lógica para verificar la validez de la respuesta
    // Por ejemplo, si una respuesta es inválida si está vacía, podrías hacer algo como esto:
    if (!question.strRespuesta || question.strRespuesta.trim() === '') {
      question.isValid = false;
    } else {
      question.isValid = true;
    }
  }
}
