import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { DescripcionActividadPage } from './descripcion-actividad.page';

const routes: Routes = [
  {
    path: '',
    component: DescripcionActividadPage,
  },
  {
    path: 'proteccion-colectiva',
    loadChildren: () =>
      import('src/app/pages/SST/proteccion-colectiva/proteccion-colectiva.module').then(m => m.ProteccionColectivaPageModule),
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class DescripcionActividadPageRoutingModule {}
